@import "typography";
@import "colours";
@import "buttons";
@import "header";
@import "footer";
@import "blocks/blocks";



@media (min-width:992px) {
    .cols-lg-2 {
        columns: 2;
    }
    .cols-lg-3 {
        columns: 3;
    }
}

.sticky {
    position: sticky;
    top: 0; // var(--h-top);
    @media (min-width: 992px) {
        top: 0; //var(--h-top-desktop);
    }
    background-color: #fff;
    z-index: 1000;
}

.breadcrumbs {
    color: var(--col-dark);
    font-size: var(--fs-300);
    a {
        text-decoration: none;
        color: inherit;
        font-weight: var(--fw-600);
        &:hover {
            color: var(--col-black);
        }
    }
}

.container-xl > .container-xl {
    padding-inline: 0;
}

.yoast-breadcrumbs {
    @extend .container-xl;
    @extend .mb-4;
    font-size: var(--fs-300);
}

a {
    transition: color var(--transition);
}

